<template>
    <MainView>
        <div slot="tool">
            <a-button type="primary" @click="message">
                参考
            </a-button>
            <a-button type="primary" style="margin-left: 5px;" @click="showSuccess">
                成功
            </a-button>
            <a-button type="primary" style="margin-left: 5px;" @click="showWarning">
                警告
            </a-button>
            <a-button type="primary" style="margin-left: 5px;" @click="showConfirm">
                确认框
            </a-button>
        </div>
        <div>
            <div>
                <a-date-picker @change="onChange" />
            </div>
            <p v-for="(item,index) in 100" :key="index">这是一个参考组件</p>
        </div>
        <a-modal title="标题" v-drag-modal :visible="visible" @ok="handleOk" @cancel="handleCancel">
            <p>参考组件</p>
            <a-form-model ref="ruleForm" :model="form" :rules="rules" :label-col="labelCol" :wrapper-col="wrapperCol">
                <a-form-model-item ref="name" label="Activity name" prop="name">
                    <a-input v-model="form.name"/>
                </a-form-model-item>
                <a-form-model-item label="Activity zone" prop="region">
                    <a-select v-model="form.region" placeholder="please select your zone">
                        <a-select-option value="shanghai">
                            Zone one
                        </a-select-option>
                        <a-select-option value="beijing">
                            Zone two
                        </a-select-option>
                    </a-select>
                </a-form-model-item>
                <a-form-model-item label="Activity time" required prop="date1">
                    <a-date-picker v-model="form.date1" show-time type="date" placeholder="Pick a date" style="width: 100%;" />
                </a-form-model-item>
                <a-form-model-item label="Instant delivery" prop="delivery">
                    <a-switch v-model="form.delivery" />
                </a-form-model-item>
                <a-form-model-item label="Activity type" prop="type">
                    <a-checkbox-group v-model="form.type">
                        <a-checkbox value="1" name="type">
                            Online
                        </a-checkbox>
                        <a-checkbox value="2" name="type">
                            Promotion
                        </a-checkbox>
                        <a-checkbox value="3" name="type">
                            Offline
                        </a-checkbox>
                    </a-checkbox-group>
                </a-form-model-item>
                <a-form-model-item label="Resources" prop="resource">
                    <a-radio-group v-model="form.resource">
                        <a-radio value="1">
                            Sponsor
                        </a-radio>
                        <a-radio value="2">
                            Venue
                        </a-radio>
                    </a-radio-group>
                </a-form-model-item>
                <a-form-model-item label="Activity form" prop="desc">
                    <a-input v-model="form.desc" type="textarea" />
                </a-form-model-item>
                <a-form-model-item :wrapper-col="{ span: 14, offset: 4 }">
                    <a-button type="primary" @click="onSubmit">
                        Create
                    </a-button>
                    <a-button style="margin-left: 10px;" @click="resetForm">
                        Reset
                    </a-button>
                </a-form-model-item>
            </a-form-model>
        </a-modal>
    </MainView>
</template>
<script>
export default {
    created() {
        console.log('created')
        console.log(abp.session)
    },
    mounted() {
        console.log('mounted')
        console.log(abp.session)
    },
    methods: {
        onChange() {

        },
        message() {
            this.visible = true;
        },
        handleOk() {
            this.visible = false;
        },
        handleCancel() {
            this.visible = false;
        },
        showSuccess() {
            this.$swal({
                title: "Good job!",
                text: "You clicked the button!",
                icon: "success",
                button: "确定",
            });
            // this.$swal("Success", "信息保存成功", "success");
        },
        showWarning() {
            this.$swal({
                title: "Good job!",
                text: "You clicked the button!",
                icon: "warning",
                button: "确定",
            });
        },
        showConfirm() {
            this.$swal({
                    title: "Are you sure?",
                    text: "Once deleted, you will not be able to recover this imaginary file!",
                    icon: "warning",
                    buttons: ["取消", "确定"],
                    // dangerMode: true,
                })
                .then((val) => {
                    if (val) {
                        this.$swal("你点击的了确定", {
                            icon: "success",
                            buttons: '确定'
                        });
                    } else {
                        this.$swal("你点击的了取消", {
                            buttons: '确定'
                        });
                    }
                });
        },
        onSubmit() {
            this.$refs.ruleForm.validate(valid => {
                if (valid) {
                    alert('submit!');
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        resetForm() {
            this.$refs.ruleForm.resetFields();
        },
    },
    data() {
        return {
            visible: false,
            labelCol: { style:"width:120px;float:left;" },
            wrapperCol: { style:"marginLeft:130px;"  },
            other: '',
            form: {
                name: '',
                region: undefined,
                date1: undefined,
                delivery: false,
                type: [],
                resource: '',
                desc: '',
            },
            rules: {
                name: [
                    { required: true, message: 'Please input Activity name', trigger: 'blur' },
                    { min: 3, max: 5, message: 'Length should be 3 to 5', trigger: 'blur' },
                ],
                region: [{ required: true, message: 'Please select Activity zone', trigger: 'change' }],
                date1: [{ required: true, message: 'Please pick a date', trigger: 'change' }],
                type: [{
                    type: 'array',
                    required: true,
                    message: 'Please select at least one activity type',
                    trigger: 'change',
                }, ],
                resource: [
                    { required: true, message: 'Please select activity resource', trigger: 'change' },
                ],
                desc: [{ required: true, message: 'Please input activity form', trigger: 'blur' }],
            },
        };

    }
}
</script>